// MediaComponent.js

import React, { useRef } from 'react';
import YouTube from 'react-youtube';

const MediaComponent = (props) => {
  const playerRef = useRef(null); // Ref to store the player object
  const isYouTube = props.mediaUrl.includes('youtube.com');
  const opts = {
    playerVars: {
      // https://developers.google.com/youtube/player_parameters
      autoplay: 0,
      fs: 0,
      loop: 1,
      showinfo: 0,
      modestbranding: 1,
      playsinline: 1,
      enablejsapi: 1,
    },
  };

  const _onReady = (event) => {
    // Access the player object and set it using the parent-provided function
    playerRef.current = event.target;
    // Optionally, you can perform actions on the player here
    // Pass the player object to the parent component
    props.setPlayer(playerRef);
  };

  return (
    <div className='wd-100'>
      {isYouTube ? (
        <YouTube videoId={extractVideoId(props.mediaUrl)} opts={opts} onReady={_onReady} iframeClassName='wd-100 height-25vh' />
      ) : (
        <video
          ref={props.mediaRef}
          src={props.mediaUrl}
          width={props.width}
          height={props.height}
          controls
          playsInline
        ></video>
      )}
    </div>
  );
};

// Function to extract video ID from YouTube URL
const extractVideoId = (url) => {
  const videoIdMatch = url.match(/[?&]v=([^&#]+)/);
  return videoIdMatch ? videoIdMatch[1] : '';
};

export default MediaComponent;
