/**
 * This file provides functions to convert IBM Watson transcript data into a standardized format.
 * It processes the transcript data, extracts and organizes words and paragraphs, and associates
 * speakers with their respective spoken words.
 */

/**
 * Searches for a matching speaker label based on the 'from' and 'to' timestamps.
 *
 * @param {Object[]} array - The array of speaker labels.
 * @param {number} from - The starting timestamp to match.
 * @param {number} to - The ending timestamp to match.
 * @returns {Object} - The matching speaker label object.
 */
function searchSpeakerLabelsByToAndFromPair(array, from, to) {
  if (!Array.isArray(array)) {
    throw new TypeError('The first argument must be an array.');
  }

  return array.filter(obj => obj['to'] === to && obj['from'] === from)[0];
}

/**
 * Normalizes a word entry from IBM Watson transcript data.
 *
 * @param {Array} IBMWord - An array representing a word [text, start, end].
 * @param {Object[]} speakerLabels - An array of speaker labels.
 * @returns {Object} - A normalized word object.
 */
function normalizeWord(IBMWord, speakerLabels) {
  if (!Array.isArray(IBMWord) || IBMWord.length < 3) {
    throw new TypeError('Expected IBMWord to be an array with at least three elements');
  }

  const [text, start, end] = IBMWord;
  const speaker = speakerLabels
      ? searchSpeakerLabelsByToAndFromPair(speakerLabels, start, end)['speaker']
      : null;

  return { start, end, text, speaker };
}

/**
 * Converts a result entry into a 2D list of normalized words.
 *
 * @param {Object} result - A result entry from IBM Watson transcript data.
 * @param {Object[]} speakerLabels - An array of speaker labels.
 * @returns {Array} - A 2D list of normalized words.
 */
function get2DWordsList(result, speakerLabels) {
  if (!Array.isArray(result)) {
    throw new TypeError('Expected result to be an array');
  }
  return result.map(({ alternatives }) => (
      alternatives.map(alt => (
          alt.timestamps.map(timestamp => (
              normalizeWord(timestamp, speakerLabels)
          ))
      ))
  ));
}

/**
 * Organizes a 2D list of words into paragraphs.
 *
 * @param {Array} wordList2d - A 2D list of words.
 * @returns {Object[]} - An array of paragraph objects.
 */
function getParagraphs(transcripts) {
  if (!Array.isArray(transcripts) || transcripts.length === 0) {
    return [];
  }

  const results = [];

  transcripts.forEach((transcript) => {
    let from = transcript['alternatives'][0]['timestamps'][0][1];
    let to = transcript['alternatives'][0]['timestamps'][transcript['alternatives'][0]['timestamps'].length - 1][2];
    results.push({start: from, end: to, speaker: 'Add speaker'});
  });

  return results;
}

/**
 * Normalizes paragraphs from IBM Watson speaker labels.
 *
 * @param {Object[]} IBMparagraphs - An array of speaker labels.
 * @returns {Object[]} - An array of normalized paragraph objects.
 */
function normalizeParagraph(transcripts, speakerLabels) {
  if (!Array.isArray(transcripts) || transcripts.length === 0) {
    return [];
  }

  const results = [];

  transcripts.forEach((transcript, index) => {
    let from = transcript['alternatives'][0]['timestamps'][0][1];
    let to = transcript['alternatives'][0]['timestamps'][transcript['alternatives'][0]['timestamps'].length - 1][2];
    let speaker = searchSpeakerLabelsByToAndFromPair(speakerLabels, from, transcript['alternatives'][0]['timestamps'][0][2])['speaker'];
    results.push({start: from, end: to, speaker: speaker});
  });

  return results;
}

/**
 * Converts IBM Watson transcript data to a standardized format.
 *
 * @param {Object} transcript - The IBM Watson transcript data.
 * @returns {Object} - An object containing an array of paragraphs and an array of words.
 */
function convertIBMWatsonToDpe(transcript) {
  const { results } = transcript;
  const results_array = results[0].results || results
  let speakerLabels = transcript.speaker_labels || transcript.results[0].speaker_labels
  speakerLabels = speakerLabels && speakerLabels.length > 0 ? speakerLabels : null;
  let wordList2d = get2DWordsList(results_array, speakerLabels);
  if (Array.isArray(wordList2d[0][0])) {
    wordList2d = wordList2d.flat()
  }
  let paragraphs = [];
  if (speakerLabels) {
    paragraphs = normalizeParagraph(results_array, speakerLabels);
  } else {
    paragraphs = getParagraphs(results_array);
  }

  return {
    paragraphs: paragraphs.flat(),
    words: wordList2d.flat().flat(),
  };
}

export { convertIBMWatsonToDpe };
