import React from "react";
import "./App.css";
import queryString from "query-string";
import "react-notifications/lib/notifications.css";
import {
  NotificationContainer,
  NotificationManager,
} from "react-notifications";
import SlateTranscriptEditor from './index.js';
import 'bootstrap-css-only';
import pako from 'pako';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      transcriptData: null,
      mediaUrl: null,
      isTextEditable: true,
      spellCheck: true,
      sttType: null,
      analyticsEvents: [],
      title: "",
      resourceTitle: "",
      fileName: "",
      autoSaveData: {},
      autoSaveContentType: "slate",
      autoSaveExtension: "json",
      isLoaded: false,
      error: false,
      aviaryUrl: "",
      formEdit: false,
      metadata: {},
    };
    this.editorRef = React.createRef();
    this.cancelEditRequest = this.cancelEditRequest.bind(this);
    this.loadTranscript = this.loadTranscript.bind(this);
    this.save = this.save.bind(this);
  }
  componentDidMount() {
    const params = queryString.parse(window.location.search);
    localStorage.setItem("transcript_finish", false);
    if (typeof params.transcript != "undefined") {
      this.loadTranscript(params);
    } else {
      this.setState({
        isLoaded: true,
        error: true,
      });
    }
    document.addEventListener("contextmenu", (event) => event.preventDefault());

    window.addEventListener("unload", async function (event) {
      localStorage.setItem("transcript_finish", true);
    });
  }
  componentDidUpdate() {
    if (this.state.formEdit) {
      window.onbeforeunload = () => true;
    } else {
      window.onbeforeunload = undefined;
    }
  }
  loadTranscript = (params) => {
    try {
      fetch(atob(params.transcript))
        .then((res) => res.json())
        .then(
          (result) => {
            let url = result.media_url;
            if (!result.env) {
              url = result.host + result.media_url;
            }
            let data = result.transcript;
            if (result.sst_type === 'ibm' || result.sst_type === 'slatejs') {
              data = JSON.parse(data);
            } 
            this.setState({
              transcriptData: data,
              mediaUrl: url,
              title: result.title,
              sttType: result.sst_type,
              isLoaded: true,
              aviaryUrl: atob(params.transcript),
              resourceTitle: result.resource_title,
              metadata: result.metadata,
            });
          },
          (error) => {
            this.setState({
              isLoaded: true,
              error: true,
            });
          }
        );
    } catch (error) {
      this.setState({
        isLoaded: true,
        error: true,
      });
    }
  };
  save = (value) => {
    this.saveTranscript(value);
  };

  saveAndClose = (value) => {
    this.saveTranscript(value, true);
  };

  saveTranscriptTitle = (newTitle) => {
    this.setState({
      title: newTitle
    });
  };

  saveMetadata = async (metadata) => {
    this.setState({
      formEdit: false,
    });
    let transcript = {
      file_transcript: {
        is_edit: false,
        is_caption: metadata.isCaption,
        is_public: metadata.isPublic,
        is_downloadable: metadata.isDownloadable,
        description: metadata.description,
        language: metadata.language,
      },
    };
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(transcript),
    };
    await fetch(this.state.aviaryUrl + ".json", requestOptions)
      .then((response) => response.json())
      .then((result) => {
        NotificationManager.success("", "Transcript Metadata saved successfully.", 3000);
      });
  };

  saveTranscript = async (value, close=false) => {
    this.setState({
      formEdit: false,
    });

    const currentContent = value;

    let transcript = {
      file_transcript: {
        slatejs: currentContent,
        is_edit: false,
        title: this.state.title
      },
      speakers: ['allison', 'watson'],
    };
    // Convert and compress the transcript
    const compressedData = pako.gzip(JSON.stringify(transcript), { to: 'string' });

    const requestOptions = {
      method: "POST",
      headers: { "Content-Encoding": "gzip" },
      body: compressedData,
    };

    await fetch(this.state.aviaryUrl + ".json", requestOptions)
      .then((response) => response.json())
      .then((_result) => {
        NotificationManager.success("", "Transcript saved successfully.", 3000);
        if (close){
          window.close();
        }
      });
  };

  cancelEditRequest = async () => {
    let r = false;
    if (this.state.formEdit) {
      r = window.confirm(
        "Changes that you made may not be saved. Are you sure you want to cancel?"
      );
    } else {
      r = true;
    }

    if (r === true) {
      this.setState({
        formEdit: false,
      });
      let transcript = {
        file_transcript: {
          is_edit: false,
        },
      };
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(transcript),
      };
      await fetch(this.state.aviaryUrl + ".json", requestOptions)
        .then((response) => response.json())
        .then((result) => {
          window.close();
        });
    }
  };

  render() {
    if (this.state.isLoaded && !this.state.error) {
      return (
        <div className="App">
          <section className="ml-lg-3">
            <SlateTranscriptEditor
              mediaUrl={this.state.mediaUrl}
              transcriptData={this.state.transcriptData}
              handleSaveEditor={this.save}
              handleSaveAndClose={this.saveAndClose}
              handleSaveTranscriptTitle={this.saveTranscriptTitle}
              isEditable={this.state.isTextEditable}
              title={this.state.title}
              resourceTitle={this.state.resourceTitle}
              autoSaveContentType='slate'
              showTitle={true}
              mediaRef={this.editorRef}
              sttType={this.state.sttType}
              metadata={this.state.metadata}
              saveMetadata={this.saveMetadata}
            />
          </section>
          <NotificationContainer />
        </div>
      );
    } else if (!this.state.isLoaded) {
      return (
        <div className="loader-container">
          <div className="loader"></div>
          <span className="loader-text">Please wait while the transcript is being loaded...</span>
        </div>
      );
    } else {
      return (
        <div> Transcript does not exist or you don't have the permission to access it. </div>
      );
    }
  }
}
export default App;
